import { GetMusicDetail } from '../../../network/music/MusicRequests'
export default {
  mounted: function () {
    this.initData()
  },
  data () {
    return {
      id: this.$attrs.data,
      musicDetail: {
        cover: '',  // 
        title: '', // 
        story_title: '', // 
        story: '', // 
        lyric: '', // 
        web_url: '', // 
        story_summary: '', // 
        author: {
          user_name: '',
          desc: ''
        }
      }
    }
  },
  methods: {
    initData: function () {
      let self = this
      let params = this.id
      new GetMusicDetail(params).start(function (data) {
        self.musicDetail = data.data.data
      })
    },
    hrefMusic: function () {
      window.location.href = this.musicDetail.web_url
    }
  }
}



