
import XHeader from 'vux/src/components/x-header/index.vue'

import { drawerRoutes } from '../../constants/Constants'
export default {
components: {
XHeader
},
props: {
title: {
type: String,
default: ''
}
},
data () {
return {
docked: true,
open: false,
headerTitle: this.title
}
},
methods: {
toggle (flag) {
this.open = !this.open
this.docked = !flag
},
route (routePage) {
this.$router.push({
name: routePage
})
this.headerTitle = drawerRoutes[routePage]
}
}
}



